html, body {
  position: relative;
  height: 100%;
}
body {
  overflow-x: hidden;
  position: relative;
  min-height: 100%;
  display: flex;
  flex-direction: column;
  margin: 0;
  padding: 0;
  font-family: 'black_jackregular', sans-serif;
  min-width: 960px;
}

